.bg-video {
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  z-index: -1;
  opacity: 0.15;

  overflow: hidden;

  &__content {
    width: 100%;
    height: 100%;

    // similar to background size
    object-fit: cover; // maintain aspect ratio fill the parent element
  }
}
