.form {
  &__group:not(:last-child) {
    margin-bottom: 2rem;
  }
  &__input {
    font-family: inherit;
    color: inherit;
    font-size: 1.5rem;
    padding: 1.5rem 2rem;
    border-radius: 2px;
    background-color: rgba($color-white, 0.5);
    border: none;

    border-bottom: 3px solid transparent;
    width: 90%;
    display: block;
    @include responsive(tab-port) {
      width: 100%;
    }

    transition: all 0.3s ease-out;

    &:focus {
      outline: none;
      box-shadow: 0 1rem 2rem rgba($color-black, 0.1);
      border-bottom: 3px solid $color-primary;
    }

    &::placeholder {
      color: $color-gray-dark-2;
    }

    &:focus:invalid {
      border-bottom: 3px solid $color-secondary-dark;
    }
  }
  &__label {
    font-size: 1.2rem;
    font-weight: 700;
    margin-left: 2rem;
    margin-top: 0.7rem;
    display: block;

    transition: all 0.3s ease-out;
  }

  // use adjacent sibling + or ~ for sibling
  &__input:placeholder-shown + &__label {
    opacity: 0;
    visibility: hidden;
    transform: translateY(-4rem);
  }

  &__radio-group {
    width: 49%;
    display: inline-block;
    @include responsive(tab-port) {
      width: 100%;
      margin-bottom: 2rem;
    }
  }
  &__radio-label {
    font-size: 1.6rem;
    cursor: pointer;

    padding-left: 4rem;

    position: relative;
  }
  &__radio-button {
    height: 3rem;
    width: 3rem;
    border: 5px solid $color-primary;
    border-radius: 50%;
    display: inline-block;
    position: absolute;
    left: 0;
    top: -0.5rem;

    &::after {
      content: '';
      height: 1.5rem;
      width: 1.5rem;
      background-color: $color-primary;
      display: block;
      border-radius: 50%;
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      opacity: 0;
      transition: opacity 0.3s ease-out;
    }
  }
  &__radio-input {
    display: none;
  }
  &__radio-input:checked + &__radio-label &__radio-button::after {
    opacity: 1;
  }
}
